// 메인화면

import { instance, instanceWithAuth } from './index';

function fetchNoticeTotalCount() {
	return instance.get(`api/notice/totalCount`);
}

function fetchNotice(page) {
	return instance.get(`api/notice/all/${page}`);
}

function fetchNoticeDetail(id) {
	return instance.get(`api/notice/detail/${id}`);
}

function addNotice(data) {
	return instanceWithAuth.post('api/admin/notice/new', data);
}

function updateNotice(id, data) {
	return instanceWithAuth.put(`api/admin/notice/update/${id}`, data);
}

function deleteNotice(data) {
	return instanceWithAuth.delete('api/admin/notice/delete', { data });
}

export {
	fetchNoticeTotalCount,
	fetchNotice,
	fetchNoticeDetail,
	addNotice,
	updateNotice,
	deleteNotice,
};
