<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/notice')" class="current">공지</li>
			<li @click="goto('/admin/news')">뉴스</li>
		</ul>

		<div class="contents-container">
			<p class="contents-title underline marB-20 body3">
				공지 수정
			</p>

			<div class="input-container">
				<div class="img-upload-container">
					<div class="input-item align-top">
						<div v-if="thumb.imgPath" class="file-preview-wrapper">
							<label for="file"> <img :src="thumb.imgPath"/></label>
							<div class="img-info">
								<span>{{ thumb.imgName }}</span>

								<div @click="fileDeleteButton">
									<Close />
								</div>
							</div>
						</div>
						<AddImageSingle
							v-else
							:refName="'mainImage'"
							@mainImage="setMainImage"
							:image="thumb"
						/>
					</div>
				</div>

				<div class="input-item">
					<p class="small-title">제목</p>
					<input
						type="text"
						class="admin-input large"
						name="title"
						value="프로그램명"
						v-model="title"
					/>
				</div>

				<div class="input-item">
					<p class="small-title">내용</p>
					<CKEditor :contents="contents" ref="editor" />
				</div>

				<button class="solid-btn green" @click="updateNotice">등록</button>
			</div>
		</div>
	</div>
</template>

<script>
import Store from '@/store';
import { mapGetters } from 'vuex';
import AddImageSingle from '@/components/common/AddImageSingle';
import CKEditor from '@/components/common/CKEditor';
import { updateNotice, fetchNoticeDetail } from '@/api/notice';
import Close from '@/assets/icon/Close.vue';

export default {
	components: { AddImageSingle, CKEditor, Close },
	computed: {
		...mapGetters(['main']),
		selectedUser() {
			console.log(
				Object.keys(this.thumb).length === 0 &&
					this.thumb.constructor === Object,
			);
			return (
				Object.keys(this.thumb).length === 0 &&
				this.thumb.constructor === Object
			);
		},
	},
	created() {
		Store.commit('SET_MAIN', null);
		this.id = this.$route.params.id;
		this.fetchNoticeDetail(this.id);
	},
	data() {
		return {
			title: '',
			contents: '',
			id: '',
			thumb: {
				imgPath: '',
				imgName: '',
			},
		};
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async fetchNoticeDetail(id) {
			try {
				let { data } = await fetchNoticeDetail(id);
				this.title = data.title;
				this.contents = data.contents;
				this.$refs.editor.setContents(data.contents);
				if (data.imgPath != null) {
					this.thumb = {
						imgPath: data.imgPath,
						imgName: data.imgName,
					};
				}
			} catch (e) {
				console.log(e);
			}
		},
		async updateNotice() {
			this.contents = this.$refs.editor.getContents();

			if (!this.title) {
				this.errMsg = '제목을 입력해주세요';
				alert(this.errMsg);
				return false;
			}
			if (!this.contents) {
				this.errMsg = '콘텐츠를 입력해주세요';
				alert(this.errMsg);
				return false;
			}
			let data = new FormData();
			if (this.main) {
				data.append('file', this.main);
			}
			data.append('title', this.title);
			data.append('contents', this.contents);

			let result;
			try {
				result = await updateNotice(this.id, data);
				console.log(result);
				if (result.status == 200) {
					alert('공지사항이 수정되었습니다');
					// this.$router.push(`/admin/notice/1`);
				} else {
					alert('에러가 발생했습니다');
				}
			} catch (e) {
				alert('에러가 발생했습니다');
			}
		},
		setMainImage(data) {
			console.log(data);
			Store.commit('SET_MAIN', data);
			this.thumb = this.main;
		},
		isEmptyObject(param) {
			return Object.keys(param).length === 0 && param.constructor === Object;
		},
		fileDeleteButton() {
			this.thumb.imgPath = '';
			this.thumb.imgName = '';
		},
	},
};
</script>

<style lang="scss">
.file-preview-wrapper {
	position: relative;
	display: inline-block;
	width: 100px;
	margin-right: 10px;
	img {
		width: 100px;
		height: 100px;
		border: 2px solid #bebebe;
		box-sizing: border-box;
	}
}
</style>
